.application_container {
    width: 100%;
}

body {
    margin: 0;
    padding: 0;
    background-color: #3f4142;
    font-size: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.columns_container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.column {
    max-width: 755px;
}

.form {
    border-color: #c1ab5d;
    border-width: 4px;
    border-style: solid;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 3px;
    margin-right: 3px;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form__header {
    color: #eeeeee;
    font-size: 14px;
    text-align: center;
    padding-top: 3px;
    padding-bottom: 3px;
    background-color: #403838;
    width: 100%;
    z-index: -1;
}

.form__body {
    color: #eeeeee;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    padding: 3px;
}
.form__body--nation {
    flex-flow: column;
}
.form__body--bless_effects {
    color: #dddddd;
    font-size: 11px;
    flex-flow: column;
    padding: 3px 0;
    
}


.form__section {
    display: flex;
    flex-flow: row nowrap;
}

.form__body--imprisonment {
    flex-direction: column;
}

.value-picker {
    display: flex;
    flex-flow: row;
    align-items: center;
    margin: 2px 2px;
}
.value-picker--magic {
  flex-flow: column;
}

.value-picker--scale {
  flex-flow: column;
  margin: 2px 1px;
}

.value-picker__label {
    font-size: 12px;
    padding: 2px 6px;
    font-family: monospace;;
    display: block;
}
.value-picker__label--bless_point {
  margin: 2px 2px;
}

.value-picker__label--magic {
    padding: 2px 5px;
}

.value-picker__label--dominion {
    background-color: #ffffee;
    color: #111111;
    padding: 2px 3px;
}

.value-picker__label--fire {
    background-color: #b03523;
}
.value-picker__label--air {
    background-color: #a38e96;
}
.value-picker__label--water {
    background-color: #331d98;
}
.value-picker__label--earth {
    background-color: #b08130;
}
.value-picker__label--astral {
    background-color: #709b96;
}
.value-picker__label--death {
    background-color: #701b63;
}
.value-picker__label--nature {
    background-color: #3d7523;
}
.value-picker__label--blood {
    background-color: #7d1b16;
}

.value-picker--dominion {
    margin-right: 12px;
    flex-flow: column;
}

.value-picker__label--scale {
    font-size: 10px;
    padding: 2px;
}

.value-picker--imprisonment {
    border-width: 0;
    margin-top: 2px;
    margin-bottom: 2px;
    padding: 0;
}

.value-picker__label--imprisonment {
    font-size: 12px;
    font-family: inherit;
    border-width: 0;
    cursor: inherit;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 0;
    padding-top: 2px;
    padding-bottom: 2px;
    padding-left: 0;
}

.value-picker__input {
    color: #eeeeee;
    font-size: 15px;
    font-weight: normal;
    font-family: monospace;
    width: 2.5em;
    background-color: inherit;
    border-width: 0;
    text-align: center;
}

.value-picker__input--magic {
    padding: 0;
}

.value-picker__input--scale {
    padding: 0;
    width: 2.4em;
    margin: 0;
}


.value-picker__input--positive{
    color: #22dd22;
}

.value-picker__input--negative{
    color: #dd2222;
}

.value-picker__input--dominion {

}

.value-picker__input--imprisonment {
    cursor: inherit;
    margin-right: 0;
    padding-right: 0;
    width: 20px;
}

.value-picker__input--imprisonment:focus {
    cursor: inherit;
    border-width: 0;
}

#debugger {
    font-family: monospace;
    font-size: 11px;
}

.debug__log {
    margin: 0 0;
}

.modal_window {
  border-color: #c1ab5d;
  border-width: 5px;
  border-style: solid;
  margin: 10px;
  border-radius: 16px;

  position: fixed;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 90%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background: #3f4142;
  display:flex;
  flex-direction: column;
}

.modal_window__header {
  margin: 0;
  padding: 5px 10px;
  text-align: center;
  background-color: #d9af8b;
}

.modal_window__close_button {
  height: 20px;
  margin: 5px 10px;
}

.modal_window__body {
  overflow-y: scroll;
}

.modal_window__list {
  margin: 5px 10px;
  font-size: 11px;

}

.modal_shroud {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 9998;
  background: rgba(64, 56, 56, 0.8);
}
